<div class="container"><h1 class="text-center my-5">Crypto Trading</h1><div class="card"><div class="card-body"><form class="row row-cols-lg-auto g-3 align-items-center" @submit.prevent="onSubmit" method="post"><div class="col-12"><label class="visually-hidden" for="inputExchange">Exchange</label><select class="form-select" id="inputExchange" v-model="exchange"><option v-for="(text, value) in exchanges" :key="value" :value="value">{{ text }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputSymbol">Symbol</label><select class="form-select" id="inputSymbol" v-model="symbol"><option v-for="value in symbols" :key="value" :value="value">{{ value }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputInterval">Interval</label><select class="form-select" id="inputInterval" v-model="interval"><option v-for="value in intervals" :key="value" :value="value">{{ value }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputIndicator">Indicator</label><select class="form-select" id="inputIndicator" v-model="indicator"><option v-for="(text, value) in indicators" :key="value" :value="value">{{ text }}</option></select></div><div class="col-12"><button class="btn btn-primary" type="submit" :disabled="loading._">Submit</button></div></form><pre class="bg-light mt-3 p-3"><code>{{ swingTrades }}</code></pre></div></div></div>